import { Pipe, PipeTransform } from '@angular/core';
import * as moment from "moment";
import 'moment/locale/ru';

const defaultOptions = {
  format:'D MMMM YYYY'
};

@Pipe({
  name: 'momentDate'
})
export class MomentDatePipe implements PipeTransform {

  transform(date: any, options:any=defaultOptions): string {
    if (typeof options === "string") {
      options = {format:options};
    }
    let format = options.format || defaultOptions.format;

    let $date = moment(date);
    switch (format) {
      case 'fromNow': return $date.calendar(null, {
        sameDay: '[Сегодня], HH:mm',
        nextDay: '[Завтра], HH:mm',
        nextWeek: 'dddd, HH:mm',
        lastDay: '[Вчера], HH:mm',
        lastWeek: '[Прошлый] dddd,  HH:mm',
        sameElse: 'DD.MM.YYYY, HH:mm'
      });
      default: return $date.format(format);
    }
  }

}



