let storedItems = localStorage.getItem('fakeChats') && JSON.parse(localStorage.getItem('fakeChats'));
let [fakeChats] = storedItems || new Array(20).fill('').reduce(([chats], el, i) => {
  chats.push(
    _merge({},responseExample,{
      id: String(Math.random()).replace('0.', ''),
      // firstName: 'Надежда',
      // lastName: 'Смирнова',
      // time: Date.now(),
      // imageUrl: "",
      // unreadAmount: Math.floor(Math.random()*3),
      // direction: Math.floor(Math.random()*2),
      // index:i
    })
  );
  return [chats];
}, [[]]);
if (!storedItems) localStorage.setItem('fakeChats',JSON.stringify([fakeChats]))


@Injectable()
export class ChatsService {

  constructor(
    public api:WsIo,
    public auth: AuthService
  ) { }

  url = 'dialog';

  /**
   * Query request
   * @param params
   * @returns {Observable<R>}
   */
  query(params) {
    let search = prepareParams(params)
    return this.api.get(this.url,{search})
      .map(response => _get(response.json(),'data',{}))
  }

  getChatsList (params) {
    return this.query(params)
      .map(response => mapResponse(
        mergeChatsResponse(response),
        this.auth.userId)
      )
  }

  // /**
  //  * Mock query request
  //  * @param params
  //  * @returns {Observable<R>}
  //  */
  // query(params) {
  //   return Observable.from(
  //     mapResponse(fakeChats)
  //   )
  // }

}

function mergeChatsResponse (data) {
  let {dialog,message,user} = data;
  return dialog.map(item=>_merge(item,{
    user: item.user_ids.map(userId=>_find(user,{id:userId})),
    message: _find(message,{id:item.message_id}),
  }));
}

function mapResponse (data:any[],currentUserId) {
  return data.map((chat =>{
    chat['_user'] = _find(chat.user,user=>user.id !== currentUserId)
    if (!_get(chat,'_user.image_url')) {
      _set(chat,'_user.image_url',require('assets/default-user.png'))
    }
    chat['_direction'] = (chat.owner_user_id == currentUserId) ? 2:1
    return chat;
  }));
}


function prepareParams (params) {
  let defaults = {
    query: '',
    filters: {
      object_id: 0,
      object_type: 0
    }
  };

  return param({
    extend: 'user,message',
  });
}





