import {Connection, Request, ReadyState,Response, ResponseOptions, RequestMethod} from "@angular/http";
import {generateRequestId, IWsRequest, Ws} from "./ws";
import {Observable} from "rxjs";
import {deparam} from "../shared/helpers/functions";

import * as url from "url";

export class WsConnection implements Connection {
  request:Request;
  response: any;
  readyState: ReadyState = ReadyState.Open;

  constructor (request:Request, ws: Ws) {
    console.warn('[WsConnection]',request)

    if (!String(request.url).startsWith("api"))
      request.url = `api/${request.url}`;

    this.request = request;
    this.response = Observable.create((observer)=>{

      let requestParams =  request.json();
      console.warn('requestParams',requestParams)

      let subscription = ws.request(
        this.prepareWsParams(request)
      ).subscribe(
        response => {
          let responseOptions = new ResponseOptions({
            body: {data:response.data},
            status: response.status
          });
          let res = new Response(responseOptions);
          observer.next(res);
        },
        error => {
          console.warn('[WsConnection] error',error)
          observer.error(error);
        },
        () => {
          this.readyState = ReadyState.Done;
          observer.complete();
        }
      );

      return function () {
        console.info('ws connection unsubscribe');
        setTimeout(()=>{
          subscription.unsubscribe();
        },5);
      }
    });
  }

  prepareWsParams (request:Request):IWsRequest {

    let body = request.json() || {};
    let parsed = url.parse(request.url) || {};
    let queryString = decodeURIComponent(decodeURIComponent(parsed.query || ''));
    let query = queryString?deparam(queryString) : '';
    let path = parsed.pathname;
    let method = (RequestMethod[request.method||RequestMethod.Get]+'').toUpperCase();
    let id = body.request_id || generateRequestId();

    return {
      path: path,
      method: method || 'GET',
      body_params: body || {},
      query_params: query,
      request_id: id
    };
  }

}



