export interface IApiUrlComponents {
  protocol?: string;
  host?: string;
  version?: string;
  path?:string;
}

export const defaultUrlComponents: IApiUrlComponents = {
  protocol: 'http',
  host: 'strizhapp.ru/api',
  version: '',
  path: ''
};

export class ApiUrl implements IApiUrlComponents {

  public protocol:string = defaultUrlComponents.protocol;
  public host:string = defaultUrlComponents.host;
  public version:string = defaultUrlComponents.version;
  public path:string = defaultUrlComponents.path;

  static build (options:IApiUrlComponents=defaultUrlComponents):string {
    let url = Object.assign({},defaultUrlComponents,options); //final options
    let version = options.version? `/${options.version}` :'';
    let path = options.path? `/${options.path}` :'';
    return `${url.protocol}://${url.host}${version}${path}`;
  }

  constructor (options:IApiUrlComponents=defaultUrlComponents) {
    Object.assign(this,defaultUrlComponents,options);
  }

  get href () {
    return ApiUrl.build(this.toJSON());
  }

  toString ():string {
    return this.href;
  }

  toJSON ():IApiUrlComponents {
    return Object.assign({},{
      protocol: this.protocol,
      host: this.host,
      version: this.version,
      path: this.path
    })
  }

}



