import { Injectable } from '@angular/core';
import { WsIo } from '../core/ws-io.service';
import { param } from '../shared/helpers/functions';
import { Observable } from 'rxjs';

const _get = require('lodash/get');
const _set = require('lodash/set');
const _merge = require('lodash/merge');
const _find = require('lodash/find');



let responseExample = {
  "id": 963,
  "user_id": 26,
  "message": "ббббб",
  "created_at": "2017-02-17 22:00:07",
  "updated_at": "2017-02-17 22:00:07",
  "deleted": false,
  "deleted_at": null,
  "dialog_id": 67,
  "object_id": 228,
  "object_type": 1,
  "file_ids": [],
  "image_ids": [],
  "location_ids": [],
  "audio_ids": [],
  "uuid": "",
  "user": {
    "id": 26,
    "first_name": "Игорь",
    "last_name": "Кит",
    "image_id": 1484630629,
    "registred_at": null,
    "is_registered": true
  }
}


let [fakeMessages] = new Array(20).fill('').reduce(([messages], c, i) => {
  messages.push(_merge({},responseExample,{
    id: Math.random().toString().replace('0.', '')
    // avatar: 'assets/fake/ava.jpg',
    // name: "Смирнова Надежда",
    // phone: "+7 (923) 564-34-21",
    // created_at: Date.now() * (1+Math.random()),
    // isMine: i%2===0,
  }));
  return [messages];
},[[],[]]);



@Injectable()
export class MessagesService {

  constructor(public api:WsIo) { }

  url = 'message';

  query(params) {
    let search = prepareParams(params);
    return this.api.get(this.url,{search})
      .map(response => _get(response.json(),'data.message',[]))
      .map(mapResponse)
  }

  // /**
  //  * Mock query
  //  * @param params
  //  * @returns {Observable<R>}
  //  */
  // query(params) {
  //   return Observable.from([fakeMessages])
  // }

}


function prepareParams (params) {
  return param(_merge({
    extend: 'user,file,image,location,audio',
    merge:true
  },params));
}


function mapResponse (data:any[]=[],currentUserId) {
  return data.map((message =>{
    if (!_get(message,'user.image_url')) {
      _set(message,'user.image_url',require('assets/default-user.png'))
    }
    return message;
  }));
}



