import {EventEmitter, Injectable} from '@angular/core';
import {FEED_TYPES} from "app/shared/types";

export interface IFeedQueryParams {
  type?: FEED_TYPES
  query?: string
  requests?: boolean
  offers?: boolean
  archived?: boolean
}

export class FeedQueryParams implements IFeedQueryParams {

  private _defaults = {
    type: FEED_TYPES.All,
    query: '',
    requests: false,
    offers: false,
    archived: false,
  };

  private _data:IFeedQueryParams = {};

  private _emit () {
    this.changes.emit(this.toJSON())
  }

  constructor(data?:any) {
    Object.assign(this._data,this._defaults,data);
  }

  public changes: EventEmitter<IFeedQueryParams> = new EventEmitter();

  get type ():FEED_TYPES { return this._data.type }
  set type (val) { this._data.type = val; this._emit(); }

  get query ():string { return this._data.query }
  set query (val) { this._data.query = val; this._emit(); }

  get requests ():boolean { return this._data.requests }
  set requests (val) { this._data.requests = val; this._emit(); }

  get offers ():boolean { return this._data.offers }
  set offers (val) { this._data.offers = val; this._emit(); }

  get archived ():boolean { return this._data.archived }
  set archived (val) { this._data.archived = val; this._emit(); }

  toJSON():IFeedQueryParams {
    return Object.assign({},this._data)
  }

}

@Injectable()
export class FeedQueryParamsService  {

  data:FeedQueryParams = new FeedQueryParams();

  constructor() { }
}



