/* VENDOR MODULES */
import {NgModule, LOCALE_ID} from '@angular/core';
import {BrowserModule} from '@angular/platform-browser';
import {AuthModule} from './auth/auth.module';

import {AppSettings, IAppSettings} from "./app.settings";

/* OWN CORE MODULES */
import {CoreModule} from './core/core.module';
import {SharedModule} from './shared/shared.module';
import {UiModule} from './ui/ui.module';
import {AppRouterModule} from './app-routing.module';

/* OWN FEATURE MODULES */
import {PersonalAreaModule} from './personal-area/personal-area.module';
import {FeedModule} from './feed/feed.module';
import {ChatsModule} from './chats/chats.module';


/* GLOBAL COMPONENTS */
import {AppComponent} from './app.component';
import {MainMenuComponent} from './main-menu/main-menu.component';

/* PAGES COMPONENTS */
import {RootPageComponent} from './root/root-page.component';
import {NotFoundComponent} from './not-found/not-found.component';
import {WsnComponent} from './wsn/wsn.component';
import {PaletteComponent} from './palette/palette.component';
import { LayoutComponent } from './layout/layout.component';

@NgModule({
  imports: [
    BrowserModule
    ,CoreModule
    ,SharedModule
    ,UiModule
    ,AppRouterModule
    ,PersonalAreaModule
    ,FeedModule
    ,ChatsModule
    ,AuthModule
  ],
  declarations: [
    AppComponent
    ,RootPageComponent
    ,MainMenuComponent
    ,NotFoundComponent
    ,WsnComponent
    ,RootPageComponent
    ,PaletteComponent
    ,LayoutComponent
  ],
  providers: [
    {provide: AppSettings, useClass: AppSettings},
    {provide: LOCALE_ID, useValue: "ru-RU"}
  ],
  bootstrap: [AppComponent]
})
export class AppModule {
}



