import {Component, OnInit, NgZone, OnDestroy} from '@angular/core';
import {ActivatedRoute, Router, NavigationExtras} from '@angular/router';
import {UserService} from '../../core/user.service';
import {FEED_TYPES} from "../../shared/types";
import {AuthService} from "../../auth/auth.service";
import {HttpIo} from "../../core/http-io.service";
import {ContactsService} from "../../resources/contacts.service";
import {PostsService} from "../../resources/posts.service";
import {FeedQueryParams, FeedQueryParamsService, IFeedQueryParams} from "../../feed/feed-query-params.service";
import { Observable, Subscription } from "rxjs";

@Component({
  selector: 'app-personal-area-layout',
  templateUrl: './personal-area-layout.component.html',
  styleUrls: ['./personal-area-layout.component.scss'],
  providers: [FeedQueryParamsService]
})
export class PersonalAreaLayoutComponent implements OnInit {

  FEED_TYPES = FEED_TYPES;
  feedType: FEED_TYPES = null;
  user: any = {};
  feedQuery:Observable<IFeedQueryParams> = null;

  constructor(
    public route: ActivatedRoute,
    public router: Router,
    public userService: UserService,
    public api: HttpIo,
    public auth: AuthService,
    public contactsService: ContactsService,
    public feedQueryService: FeedQueryParamsService,
  ) {
  }

  ngOnInit() {

    this.route.queryParams.subscribe(
      queryParams => {
        console.warn('route queryParams', queryParams);
        if (!queryParams['feedType']) {
          this.feedQueryService.data.type = FEED_TYPES.My
          setTimeout(()=>{
            this.router.navigate([], {
              queryParams: {feedType: FEED_TYPES.My},
              replaceUrl: true
            })
          },50)
        } else {
          this.feedQueryService.data.type = queryParams['feedType']
        }
      }
    );

    this.contactsService.getAllCurrentUserContacts('page_size=6').subscribe();

    this.feedQuery = this.feedQueryService.data.changes
      .startWith(this.feedQueryService.data.toJSON())
  }

}



