import {Injectable} from '@angular/core';
import {Observable} from "rxjs";
import {HttpIo} from '../core/http-io.service';
import {WsIo} from '../core/ws-io.service';
import {deparam, param} from "../shared/helpers/functions";
import { IFeedQueryParams } from '../feed/feed-query-params.service';
import { FEED_TYPES } from '../shared/types';

const _get = require('lodash/get');
const _set = require('lodash/set');
const _merge = require('lodash/merge');
const _defaultsDeep = require('lodash/defaultsDeep');

let _usersFeed = [];

@Injectable()
export class PostsService {

  url = 'post';

  constructor(public api: WsIo) {}

  get $usersFeed() {
    // return _usersFeed.map(el=>Object.assign({},el))
    return _usersFeed;
  }

  getCurrentUserFeed(): Observable<any> {
    let search = param({
      page_size: 50,
      order: {
        created_at: 'desc'
      }
    });

    return this.api.get(this.url, {search})
      .map(el => el.json().data.post)
      .do(res => _usersFeed = res)
  }

  query(params){
    let search = prepareFeedParams(params);

    return this.api.get(this.url, {search})
      .map(el => el.json().data.post)
  }

}

function prepareFeedParams (params:any={}):string {

  let defaults = {
    query: '',
    // filters: {
    //   // feed: false,
    //   // is_favorite: false
    // },
    // conditions: {
    //   // user_id: { '<>':1 }
    // },
    order: {
      created_at: 'desc'
    },
    page_size: 50
  };

  let resultParams = _defaultsDeep({
    query: params.query
  },defaults);

  switch (Number(params.type)) {
    case FEED_TYPES.All:
      _set(resultParams,'filters.feed',true);
      break;
    case FEED_TYPES.Archive:
      break;
    case FEED_TYPES.Favourite:
      _set(resultParams,'filters.is_favorite',true);
      break;
    case FEED_TYPES.My:
      break;
  }



  console.warn(`=====`,resultParams,`====`)
  return param(resultParams)
}



