import { Component, OnInit, Input, OnChanges, SimpleChanges } from '@angular/core';
import { FEED_TYPES } from "../../types";
import {PostsService} from "../../../resources/posts.service";
// import {FeedQueryParams, FeedQueryParamsService, IFeedQueryParams} from "../../../feed/feed-query-params.service";
import {Subject} from "rxjs";
const pick = require('lodash/pick');
const merge = require('lodash/merge');

@Component({
  selector: 'app-shared-feed-list',
  templateUrl: './shared-feed-list.component.html',
  styleUrls: ['./shared-feed-list.component.scss']
})
export class SharedFeedListComponent implements OnInit {

  @Input() type:FEED_TYPES = FEED_TYPES.My;

  @Input() items = [];

  // @Input() query: IFeedQueryParams = {};
  @Input() query: any = {};

  FEED_TYPES = FEED_TYPES;

  isLoading = true;

  reloadRequests = new Subject();

  constructor(
    public postsService: PostsService
  ) { }

  ngOnInit() {

    this.reloadRequests
      .do(()=>this.isLoading = true)
      .debounceTime(300)
      .flatMap(query=>this.reload(query))
      .do(()=>{this.isLoading = false})
      .subscribe(res => this.items = res)

    this.reloadRequests.next(this.query)

  }

  ngOnChanges (changes:SimpleChanges={}) {
    changes.query && changes.query.currentValue &&
    this.reloadRequests.next(changes.query.currentValue)
  }

  private reload (query) {
    return this.postsService.query(query)
  }

}



