import {Injectable, NgZone} from '@angular/core';
import {Observable} from "rxjs";

import {HttpIo} from "../core/http-io.service"
import {WsIo} from "../core/ws-io.service"

export interface IContact {
  id: number,//идентификатор контакта
  phone: string,
  user_id: number,
  first_name: string,
  last_name:string,
  created_at: string,
  updated_at: null,
  deleted: false,
  deleted_at: null,
  contact_group_id: number,//идентификатор группы, пока не используем
  contact_user_id: number,//идентификатор пользователя в системе
  is_registered: true,//признак зарегистрированности пользователя
  contact_group_ids: any,//идентификатор группы (не используем)
  type: number,//тип контакта, (пока не используем)
}

let _contacts = [];

@Injectable()
export class ContactsService {

  url = 'contact';

  constructor(
    public http:HttpIo,
    public ws:WsIo,
    public zone: NgZone
  ) { }

  public get $contacts ():IContact[] {
    // return _contacts.map(el=>Object.assign({},el))
    return _contacts
  }

  getAllCurrentUserContacts (search:string=''):Observable<IContact[]> {
    return this.ws.get(this.url,{search})
      .map(el=>el.json().data.contact)
      .do(
        contacts=>{
          console.warn('[CONTACTS RESPONSE]',contacts)
          _contacts = contacts;
        },
        error=>{
          console.warn('[CONTACTS error]',error)
        },
        ()=>{
          console.warn('[CONTACTS complete]')
        }
      )
  }

}



