import {NgModule} from '@angular/core';
import {
  RouterModule, Routes, Resolve, ActivatedRouteSnapshot, RouterStateSnapshot, Router,
  CanActivate, CanActivateChild
} from '@angular/router';

import {WsnComponent} from './wsn/wsn.component';
import {RootPageComponent} from './root/root-page.component';
import {NotFoundComponent} from './not-found/not-found.component';
import {PersonalAreaLayoutComponent} from './personal-area/personal-area-layout/personal-area-layout.component';
import {FeedLayoutComponent} from './feed/feed-layout/feed-layout.component';
import {ChatsLayoutComponent} from './chats/chats-layout/chats-layout.component';
import {PaletteComponent} from './palette/palette.component';

import {Injectable} from "@angular/core";
import {Observable} from "rxjs";
import {HttpIo} from "./core/http-io.service";
import {UserService} from "./core/user.service";
import {AuthService} from "./auth/auth.service";
import {LayoutComponent} from "./layout/layout.component";
import {AuthFormComponent} from "./auth/auth-form/auth-form.component";
import {AuthLayoutComponent} from "./auth/layout/layout.component";
import {AuthGuard} from "./core/routerGuards";
import {UserResolver} from "./core/routerResolvers";

export const appRoutes: Routes = [
  {
    path: '',
    component: LayoutComponent,
    children: [
      {
        path: '',
        resolve: {
          userService: UserResolver
        },
        canActivateChild: [AuthGuard],
        children: [
          {
            path: '',
            redirectTo: 'personal-area',
            pathMatch: 'full'
          },
          {
            path: 'personal-area',
            component: PersonalAreaLayoutComponent
          },
          // {
          //   path: 'personal-area?feedType',
          //   component: PersonalAreaLayoutComponent
          // },
          {
            path: 'feed',
            component: FeedLayoutComponent
          },
          {
            path: 'chats',
            component: ChatsLayoutComponent
          },
          {
            path: 'chats/:id',
            component: ChatsLayoutComponent
          }
        ]
      },
      {
        path: 'wsn',
        component: WsnComponent,
        canActivate: [AuthGuard]
      },
      {
        path: 'palette',
        component: PaletteComponent
      },

    ]
  },
  {
    path: 'auth',
    component: AuthLayoutComponent,
  },
  {
    path: '**',
    component: NotFoundComponent
  },
];

@NgModule({
  imports: [
    RouterModule.forRoot(appRoutes)
  ],
  exports: [
    RouterModule
  ],
  providers: [
    AuthGuard,
    UserResolver
  ]
})
export class AppRouterModule {}



