import {XHRBackend, ConnectionBackend, Request, XHRConnection} from "@angular/http";
import {HttpIo} from "./http-io.service";
import {ApiUrl,IApiUrlComponents} from './api-url';
import {serializeObjToQuery} from "./serializeObjToQuery";
export class HttpIoXHRBackend extends XHRBackend implements ConnectionBackend {

  createConnection(request: Request): XHRConnection {
    this.prepareRequest(request);

    let connection = super.createConnection(request)

    return connection;
  };

  prepareRequest (request: Request):Request {
    console.warn('[===========prepare request========= ]');
    console.log(request);

    if (!this.isUrlAbsolute(request.url)) {
      request.url = ApiUrl.build({
        path: request.url
      });
    }

    console.warn('[==================== ]');
    return request;
  }


  isUrlAbsolute (url:string=''):boolean {
    return url.startsWith('http');
  }

}



