import {ConnectionBackend} from "@angular/http";
import {Ws} from "./ws";
import {WsConnection} from "./ws-connection";

export class WsBackend implements ConnectionBackend {

  constructor (
    public ws: Ws
  ) {
    console.warn('[WsBackend]',this.ws)

  }

  createConnection (request:any):WsConnection {
    console.warn('[WsBackend.createConnection]',request)
    return new WsConnection(request,this.ws)
  }
}



