import { Component, OnInit, OnDestroy } from '@angular/core';

const BODY_FULLHEIGHT_CLASS = 'full-height';

@Component({
  selector: 'app-layout',
  templateUrl: './layout.component.html',
  styleUrls: ['./layout.component.scss']
})
export class AuthLayoutComponent implements OnInit,OnDestroy {

  constructor() { }

  ngOnInit() {
    document.documentElement.classList.add(BODY_FULLHEIGHT_CLASS);
  }

  ngOnDestroy () {
    document.documentElement.classList.remove(BODY_FULLHEIGHT_CLASS);
  }

}



