import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../shared/shared.module';
import { UiModule } from '../ui/ui.module';
import { FeedLayoutComponent } from './feed-layout/feed-layout.component';
import { FeedListComponent} from './feed-list/feed-list.component';
import { FeedFilterComponent } from './feed-filter/feed-filter.component';
import { FeedItemComponent } from './feed-item/feed-item.component';
import {FeedQueryParamsService} from "./feed-query-params.service";

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    UiModule
  ],
  declarations: [
    FeedLayoutComponent,
    FeedFilterComponent,
    FeedListComponent,
    FeedItemComponent
  ],
  providers: [
    FeedQueryParamsService
  ]
})
export class FeedModule { }



