import { Component, OnInit, Input, ViewChild, ViewRef, ElementRef } from '@angular/core';
import { MessagesService } from '../../resources/messages.service';
import { Observable } from 'rxjs';
import 'rxjs';

const _debounce = require('lodash/debounce');

let [fakePromoList] = new Array(20).fill('').reduce(([promos], c, i) => {
  if (promos.length < 1) promos.push({
    avatar: 'assets/fake/ava.jpg',
    name: "Смирнова Надежда",
    phone: "+7 (923) 564-34-21",
    id: Math.random().toString().replace('0.', '')
  });
  return [promos];
}, [[]]);

@Component({
  selector: 'app-chat',
  templateUrl: './chat.component.html',
  styleUrls: ['./chat.component.scss']
})
export class ChatComponent implements OnInit {

  @Input() chatId = null;

  @ViewChild('scrollable') scrollable: ElementRef

  messages: Observable<any[]>;

  promoList = fakePromoList;

  isReloading = false;

  constructor(public messagesResource: MessagesService) {
    this.reload = _debounce(this.reload.bind(this), 50)
  }

  ngOnInit() {
    this.reload();
  }

  ngOnChanges(changes) {
    if (changes['chatId']) {
      this.reload();
    }
  }

  reload() {
    this.isReloading = true;
    // this.messages = this.messagesResource.query({
    setTimeout(()=>{
      this.messages = this.messagesResource.query({
        filters: {
          dialog_id: this.chatId
        }
      }).do(
        () => {
          setTimeout(() => {
            let el = this.scrollable.nativeElement;
            el.scrollTop = el.scrollHeight
            this.isReloading = false;
          }),
            (err) => {
              console.warn('err', err)
              this.isReloading = false;
            }
        });
    },100)
  }

}



