import {Injectable} from '@angular/core';
import {HttpIo} from "./http-io.service";
import {Observable, Observer, BehaviorSubject} from "rxjs";
import 'rxjs/add/operator/share';
import {WsIo} from "./ws-io.service";

export interface IUser {
  id: number,
  user_id: number,
  login: string,
  birthday: string,
  sex: number,
  marriage: number,
  city: number,
  city_id: number,
  email: string,
  is_email_confirmed: boolean,
  personal_phone_number: string,
  phone: string,
  first_name: string,
  last_name: string,
  nickname: string,
  offer_version: number,
  is_offer_accepted: boolean,
  balance: string,
  personal_phone_status: number,
  personal_info_status: number,
  updated_at: number,
  created_at: number,
  image_id:number,
  image_url:string,
  settings: {
    notifications: {
      accept_untrusted: boolean,
      receive_sms: boolean
    }
  },
  company_id: number,
  university_id: number,
  faculty_id: number
}


let USER: IUser = {} as IUser;
let USER_STATE = {
  isUpdating: false
};

@Injectable()
export class UserService {

  public subject: BehaviorSubject<IUser>;
  subscribe: Function;

  public get USER(): IUser {
    return Object.assign({}, USER) as IUser
  }

  public get USER_STATE() {
    return Object.assign({}, USER_STATE)
  };

  constructor(public api: WsIo) {
    this.subject = new BehaviorSubject(undefined);

    this.subscribe = (onData,onError,onComplete) =>{
      return this.subject.subscribe(onData,onError,onComplete);
    };

    this.subscribe(
        (user)=>{
          console.log('userService changes',user);
          USER = user;
        },
        (error)=>{
          console.warn('userService error',error)
        },
        ()=>{
          console.log('userService complete')
        },
    );
  }

  get isUpdating() {
    return this.USER_STATE.isUpdating;
  }

  update(id:number=USER.id) {

    USER_STATE.isUpdating = true;
    let observable = this.api.get(`user/${id}`)
      .map(r => r.json().data).do(
        response => {
          // Object.assign(USER, userService);
          this.subject.next(response as IUser);
        },
        err => {
          console.warn('can not get userService', err);
        },
        () => {
          USER_STATE.isUpdating = false
        }
      );
    return observable;
  }

  delete() {
    this.subject.next(null);
  }

}



