import {Component, OnInit, NgZone} from '@angular/core';
import {ActivatedRoute, Router} from "@angular/router";
import {UserService} from "../core/user.service";
import {HttpIo} from "../core/http-io.service";
import {AuthService} from "../auth/auth.service";
import {WsIo} from "../core/ws-io.service";

@Component({
  selector: 'app-layout',
  templateUrl: './layout.component.html',
  styleUrls: ['./layout.component.scss']
})
export class LayoutComponent implements OnInit {

  constructor(public route: ActivatedRoute,
              public router: Router,
              public userService: UserService,
              public api: HttpIo,
              public auth: AuthService,
              public apiWs: WsIo,
              public zone: NgZone) {

    window['api'] = this.api;
    window['apiWs'] = this.apiWs;
    window['userService'] = this.userService;
    window['el'] = this;
    window['auth'] = this.auth;
    window['zone'] = this.zone;

  }

  onLogoutClick (e) {
    e.preventDefault();
    console.log('/')
    this.logout();
    return false;
  }

  logout () {
    this.auth.logout().subscribe(
      (ok)=>{ console.info('logout ok') },
      (fail)=>{ console.warn('logout fail',fail) },
      ()=>{ console.info('logout complete') }
    )
  }

  ngOnInit() {
  }

}



