import {NgModule, Optional, SkipSelf, LOCALE_ID} from '@angular/core';
import {SharedModule} from '../shared/shared.module';
import {UiModule} from '../ui/ui.module';
import {AppRouterModule} from '../app-routing.module';
import {ResourcesModule} from '../resources/resources.module';
import {ApiUrl} from './api-url';
import {SettingsService} from './settings.service';
import { Ws } from './ws';
import {WsBackend} from "./ws-backend";

import {HttpIo} from "./http-io.service";
import {ApiBaseRequestOptions, ApiRequestOptions} from "./api-request-options";
import {XHRBackend, Http, BaseRequestOptions,RequestOptions, ConnectionBackend, XHRConnection, Request} from "@angular/http";
import {apiFactory} from "./http-io.factory";
import {apiWsFactory} from "./ws-io.factory";
import {UserService} from "./user.service";
// import {BaseWsRequestOptionsCustom} from "./ws-request-options";
import {WsIo} from "./ws-io.service";
import {HttpIoXHRBackend} from "./http-io-backend";
import {wsBackendFactory} from "./ws-backend.factory";
import {AuthService} from "../auth/auth.service";

let importsAndExports = [
  SharedModule
  ,UiModule
  ,AppRouterModule
  ,ResourcesModule
];


@NgModule({
  imports: [
    ...importsAndExports
  ],
  declarations: [],
  exports: [
    ...importsAndExports
  ],
  providers: [
    XHRBackend,
    HttpIoXHRBackend,
    BaseRequestOptions,
    ApiBaseRequestOptions,
    // BaseWsRequestOptionsCustom,
    SettingsService,
    Ws,
    // {
    //   provide: BaseRequestOptions,
    //   useClass: ApiBaseRequestOptions
    // },
    // {
    //   provide: RequestOptions,
    //   useValue: ApiRequestOptions
    // },
    {
      provide: WsBackend,
      useFactory: wsBackendFactory,
      deps:[Ws]
    },
    {
      provide: HttpIo,
      useFactory: apiFactory,
      deps: [HttpIoXHRBackend, ApiBaseRequestOptions]
      // deps: [WsBackend, BaseWsRequestOptionsCustom]
    },
    {
      provide: WsIo,
      useFactory: apiWsFactory,
      // deps: [WsBackend, BaseWsRequestOptionsCustom]
      deps: [WsBackend, BaseRequestOptions]
    },
    {
      provide: "ApiUrl",
      useValue: ApiUrl
    },
    {provide:LOCALE_ID,useValue:'ru'},
    UserService,
    AuthService
  ]

})
export class CoreModule {
  constructor(@Optional() @SkipSelf() parentModule: CoreModule) {
    if (parentModule)
      throw new Error('CoreModule is already loaded. Import it in the AppModule only');
  }
}



