import {Component, OnInit, NgZone} from '@angular/core';
import {Router, ActivatedRoute} from '@angular/router';
import {HttpIo} from "../../core/http-io.service";
import {WsIo} from "../../core/ws-io.service";
import {UserService, IUser} from "../../core/user.service";
import {AuthService} from "../auth.service";

interface IAuthModel {
  phone: string,
  code: string
}

enum STATE_NAMES {Phone,Code}
const LAST_STATE_NAME = STATE_NAMES.Code
const STATES = {
  [STATE_NAMES.Phone]: {

  },
  [STATE_NAMES.Code]: {

  }
};


@Component({
  selector: 'app-auth-form',
  templateUrl: './auth-form.component.html',
  styleUrls: ['./auth-form.component.scss']
})
export class AuthFormComponent implements OnInit {

  constructor(public api: AuthService,
              public router: Router,
              public apiWs: WsIo,
              public userService: UserService,
              public auth: AuthService,
              public zone:NgZone) {
    window['update'] = (data:any={}) => {
      this.zone.run(()=>{
        Object.assign(this,data)
      })
    }
  }

  form:HTMLFontElement;
  user: IUser = null;

  model: IAuthModel = {
    phone: '',
    code: ''
  };

  STATES = STATES;
  STATE_NAMES = STATE_NAMES;
  currentState: STATE_NAMES = STATE_NAMES.Phone;

  isSubmitting:boolean = false;

  get isLastState () { return this.currentState === STATE_NAMES.Code };

  get isPhoneTooltipVisible ():boolean {
    return !!this._isPhoneTooltipVisible;
  };

  get isPhoneInputError () {
    return !!this._isPhoneInputError;
  }

  get isModelValid() {
    return Boolean(this.model.phone);
  }

  get submitBlockedTime () {
    // return Date.now();
    return '00:29';
  }

  get isSubmitDisabled () {
    return !!this.submitBlockedTime && this.isSubmitting
  }

  public _user:any = {};

  private _isPhoneTooltipVisible = false;
  private _isPhoneInputError = false;

  ngOnInit() {
    window['api'] = this.api;
    window['router'] = this.router;
    window['apiWs'] = this.apiWs;
    window['userService'] = this.userService;
    window['auth'] = this.auth;
    window['zone'] = this.zone;

    // this.setState(STATE_NAMES.Phone);
  }

  setState (stateName: STATE_NAMES) {
    Object.assign(this,STATES[stateName],{
      currentState: Number(stateName)
    })
  }

  setNextState() {
    let nextState = (()=>{
      switch (this.currentState) {
        case STATE_NAMES.Phone: return STATE_NAMES.Code;
        default: return STATE_NAMES.Phone;
      }
    })();
    console.warn('mew state',STATE_NAMES[nextState]);
    this.setState(nextState)
  }

  submit(e) {
    console.log('submit', e, this.model,!this.isModelValid, this.isSubmitting);
    if (!this.isModelValid) return false;

    let observable;
    if (this.model.phone && this.model.code) {
      observable = this.api.login(
        this.model.phone,
        this.model.code
      )
    } else {
      observable = this.api.requestCode(
        this.model.phone
      )
    }

    observable.subscribe(
      response => {
        console.info('response', response,this.currentState)
        this.zone.run(()=>{

          if (this.currentState !== LAST_STATE_NAME) return this.setNextState();
          this.router.navigate(['/personal-area']);

        })
      },
      error => {
        console.warn('error', error)
      },
      ()=> this.zone.run(()=>{
        console.warn('request complete')
      })
    );
  }
}



