import {Component, OnDestroy, OnInit} from '@angular/core';
import {ActivatedRoute, Router, NavigationExtras} from '@angular/router';
import {FEED_TYPES} from "app/shared/types";
import {UserService} from "app/core/user.service";
import {PostsService} from "app/resources/posts.service";
import { FeedQueryParams, FeedQueryParamsService, IFeedQueryParams } from "../feed-query-params.service";
import { Observable, Subscription } from "rxjs";

@Component({
  selector: 'app-feed-layout',
  templateUrl: './feed-layout.component.html',
  styleUrls: ['./feed-layout.component.scss'],
  providers: [FeedQueryParamsService]
})
export class FeedLayoutComponent implements OnInit {

  FEED_TYPES = FEED_TYPES;
  feedType: FEED_TYPES = FEED_TYPES.All;
  feedQuery:Observable<IFeedQueryParams> = null;

  constructor(
    public route:ActivatedRoute,
    public router: Router,
    public userService: UserService,
    public postsService: PostsService,
    public feedQueryService: FeedQueryParamsService
  ) { }

  ngOnInit() {
    this.route.params.subscribe(
      params => console.warn('route params', params)
    );
    this.route.queryParams.subscribe(
      queryParams => {
        console.warn('route queryParams', queryParams);
        if (!queryParams['feedType']) {
          this.feedQueryService.data.type = FEED_TYPES.All;
          setTimeout(()=>{
            this.router.navigate([], {
              queryParams:{feedType:FEED_TYPES.All},
              replaceUrl:true
            })
          },50)
        } else {
          this.feedQueryService.data.type = queryParams['feedType']
        }
      }
    );

    this.feedQuery = this.feedQueryService.data.changes
      .startWith(this.feedQueryService.data.toJSON())
  }

}



