import { Component, OnInit, Input } from '@angular/core';
import {CHAT_DIRECTIONS} from "../../shared/types";
import { IUser } from '../../core/user.service';


export interface IChatItem {
  id:number,
  object_id:number,
  created_at: string,
  updated_at: string,
  deleted: false,
  deleted_at: null,
  message_id:number,
  user_ids: number[],
  object_type:number,
  title: string,
  owner_user_id:number,
  last_read_message: Object,
  unread_message: Object,
  post_id: number,
  last_read_message_id: number,
  max_last_read_message_id: number,
  unread_message_count:number,
  _user: IUser
}

@Component({
  selector: 'app-chat-item',
  templateUrl: './chat-item.component.html',
  styleUrls: ['./chat-item.component.scss']
})
export class ChatItemComponent implements OnInit {

  @Input() item:IChatItem = null;

  CHAT_DIRECTIONS = CHAT_DIRECTIONS;

  constructor() {}

  ngOnInit() {
  }

}



