import { Component, OnInit,OnDestroy, ElementRef } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import {Subscription} from "rxjs/Subscription";
import {UserService} from "../../core/user.service";
import { ChatsService } from '../../resources/chats.service';
import { MessagesService } from '../../resources/messages.service';

@Component({
  selector: 'app-chats-layout',
  templateUrl: './chats-layout.component.html',
  styleUrls: ['./chats-layout.component.scss']
})
export class ChatsLayoutComponent implements OnInit,OnDestroy {

  chatId = null;
  height = '100%';

  resizeEvents: Subscription;

  // chats = [];
  // messages = [];
  chats: Observable<any[]>;
  messages: Observable<any[]>;

  constructor(
    public el: ElementRef,
    public route: ActivatedRoute,
    public userService: UserService,
    public chatsResource: ChatsService,
    public messagesResource: MessagesService
  ) { }

  ngOnInit() {

    this.route.params.subscribe(
      params => this.chatId = params['id']
    );

    this.updateHeight()
    this.resizeEvents = Observable.fromEvent(window,"resize")
      .debounce((x)=>Observable.timer(10))
      .subscribe( this.updateHeight.bind(this) );

    this.chats = this.chatsResource.getChatsList({});

  }

  updateHeight () {
    console.warn('updateHeight');
    let paddingBottom = 20;
    let height = document.documentElement.clientHeight - this.el.nativeElement.offsetTop - paddingBottom;
    this.height = `${height}px`;
  }

  ngOnDestroy () {
    this.resizeEvents.unsubscribe();
  }

}



