import {BaseRequestOptions, URLSearchParams, RequestOptions, RequestOptionsArgs, Headers, QueryEncoder} from "@angular/http";
import {ApiUrl} from "./api-url";
import {serializeObjToQuery} from "./serializeObjToQuery";

export class ApiRequestOptions extends RequestOptions {
  constructor(options?: RequestOptionsArgs) {
    // {method, headers, body, url, search, withCredentials, responseType}
    console.warn('[ApiRequestOptions constructor]',options)
    super(options);
  }
  merge (options) {
    console.warn('[ApiRequestOptions]',options)
    return super.merge(options);
  }
}

export class ApiBaseRequestOptions extends BaseRequestOptions {

  // default request options
  merge(options:any) {
    console.warn('[ApiBaseRequestOptions]',options)

    // if (options.search && !(options.search instanceof URLSearchParams)) {
    //   options.search = new URLSearchParams(
    //     serializeObjToQuery(options.search)
    //   );
    // }

    options.withCredentials = true;
    let merged = super.merge(options);

    return merged;
  }
}



