import {NgModule} from '@angular/core';
import {HttpModule, Http, XHRBackend, BaseRequestOptions} from '@angular/http';
import {FormsModule} from '@angular/forms';
import {CommonModule} from '@angular/common';

import {AppRouterModule} from '../app-routing.module';
import { SharedFeedListComponent } from './components/shared-feed-list/shared-feed-list.component';
import { SharedFeedItemComponent } from './components/shared-feed-item/shared-feed-item.component';
import { MomentDatePipe } from './moment-date.pipe';
import { PhonePipe } from './phone.pipe';

let importsAndExports = [
  CommonModule,
  FormsModule,
  AppRouterModule
];

let declarationsEndExports = [
  SharedFeedListComponent
  ,SharedFeedItemComponent
  ,MomentDatePipe
  ,PhonePipe
]

@NgModule({
  imports: [
    ...importsAndExports
  ],
  declarations: [
    ...declarationsEndExports
  ],
  exports: [
    ...importsAndExports
    ,...declarationsEndExports
    ,FormsModule
    ,HttpModule
  ]
})
export class SharedModule {
}



