import {Injectable} from '@angular/core';
import {Observable} from 'rxjs/Observable';
import "rxjs/add/operator/do";
import "rxjs/add/operator/share";

import {HttpIo} from "../core/http-io.service";
import {UserService} from "../core/user.service";
import {Router} from "@angular/router";
import {WsIo} from "../core/ws-io.service";
import {Ws} from "../core/ws";

enum CODE_TYPES {SMS = 1, Application = 2}

let authData = null;
let deviceToken = (() => ('' + Math.random()).replace('0.', ''))();

@Injectable()
export class AuthService {

  constructor(private api: HttpIo,
              private wsApi: WsIo,
              private ws: Ws,
              private userService: UserService,
              private router: Router) {
  }

  isAuthorized(): boolean {
    return !!authData;
  }

  get userId() {
    return authData && authData.user_id;
  }

  // custom methods implementation
  auth(): Observable<any> {
    if (authData) {
      return Observable.of(authData);
    }
    return this.api.get('auth').map(response => response.json().data).do(
      result => {
        if (result.user_id) authData = result;
        this.ws.connect();
        return result
      }
    );
  }

  requestCode(login): Observable<any> {
    return this.api.post('code', {
      "phone": login,
      "device_type": "web",
      "device_token": deviceToken,
      "code_type": CODE_TYPES.SMS
    }).map(el => el.json())
      .do(result => {
        if (result.user_id) authData = result.data;
      });
  }

  login(login, password): Observable<any> {
    return this.api.post('auth', {
      "phone": login,
      "device_type": "web",
      "device_token": deviceToken,
      "code": password,
      "type": "code",
      "system_version": "10.2.2",
      "application": "com.app",
      "application_version": "0.0.1"
    }).map(el => el.json())
      .do(result => {
        if (result.user_id) authData = result.data;
      });

  };

  logout(): Observable<any> {
    authData = null;
    return this.api.delete('auth').map(el => el.json().data)
      .do(result => {
        this.userService.delete();
        if (this.ws.connected) this.ws.socket.disconnect();
        this.router.navigate(["/auth"])
      });
  };

}



