/**
 * @function param
 *
 * Takes a string of name value pairs and returns a Object literal that represents those params.
 * Takes an Object literal and returns a string of name value pairs that represents those params.
 *
 * @param {Object} A JavaScript Object that represents the params
 * @return {String} params a string like <code>"foo=bar&person[age]=3"</code>
 *
 */

export const param = (()=>{

  return function param (obj:Object,prefix?):string {

    var str = [], p;
    for(p in obj) {
      if (obj.hasOwnProperty(p)) {
        var k = prefix ? prefix + "[" + p + "]" : p, v = obj[p];
        str.push((v !== null && typeof v === "object") ?
          param(v, k) :
          encodeURIComponent(k) + "=" + encodeURIComponent(v));
      }
    }
    return str.join("&");

  };
})();


/**
 * @function deparam
 *
 * Takes a string of name value pairs and returns a Object literal that represents those params.
 *
 * @param {String} params a string like <code>"foo=bar&person[age]=3"</code>
 * @return {Object} A JavaScript Object that represents the params:
 *
 *     {
		 *       foo: "bar",
		 *       person: {
		 *         age: "3"
		 *       }
		 *     }
 */
export const deparam = (()=>{

  var digitTest = /^\d+$/,
    keyBreaker = /([^\[\]]+)|(\[\])/g,
    plus = /\+/g,
    paramTest = /([^?#]*)(#.*)?$/;

  return function deparam (params:string=''):Object {

    if(!params || !paramTest.test(params) ) {
      return {};
    }

    var data = {},
      pairs = params.split('&'),
      current;

    for(var i=0; i < pairs.length; i++){
      current = data;
      var pair = pairs[i].split('=');

      // if we find foo=1+1=2
      if(pair.length != 2) {
        pair = [pair[0], pair.slice(1).join("=")]
      }

      var key = decodeURIComponent(pair[0].replace(plus, " ")),
        value = decodeURIComponent(pair[1].replace(plus, " ")),
        parts = key.match(keyBreaker);

      for ( var j = 0; j < parts.length - 1; j++ ) {
        var part = parts[j];
        if (!current[part] ) {
          // if what we are pointing to looks like an array
          current[part] = digitTest.test(parts[j+1]) || parts[j+1] == "[]" ? [] : {}
        }
        current = current[part];
      }
      let lastPart = parts[parts.length - 1];
      if(lastPart == "[]"){
        current.push(value)
      }else{
        current[lastPart] = value;
      }
    }
    return data;
  }
})();



