import { Component, OnInit,Input } from '@angular/core';

interface IImage {
  id: number,
  title: string,
  url: string,
  path: string,
  created_at: string,
  updated_at: string,
  deleted: false,
  deleted_at: null,
  mime_type: string,
  type: number,
  md5: string
}

interface IFeedItem {
  id: number,
  title: string,
  description: string,
  image_ids: Array<number>,
  price: string,
  date_from: null,
  is_profitable: null,
  user_id: number,
  created_at: string,
  updated_at: string,
  deleted: false,
  deleted_at: null,
  user_ids: Array<number>,
  type: number,
  is_archived: false,
  date_to: null,
  dialog_count: number,
  file_ids: Array<number>,
  location_ids: Array<number>,
  price_description: string,
  profit_description: string,
  is_favorite: true,
  image_urls: Array<string>
  image?: Array<IImage>
}

let feedItemDefaults:IFeedItem =  {
  id: 0,
  title: "Новое",
  description: "Название",
  image_ids: [],
  price: "",
  date_from: null,
  is_profitable: null,
  user_id: 1,
  created_at: "",
  updated_at: "",
  deleted: false,
  deleted_at: null,
  user_ids: [],
  type: 1,
  is_archived: false,
  date_to: null,
  dialog_count: 0,
  file_ids: [],
  location_ids: [],
  price_description: "",
  profit_description: "",
  is_favorite: true,
  image_urls: []
};

@Component({
  selector: 'app-shared-feed-item',
  templateUrl: './shared-feed-item.component.html',
  styleUrls: ['./shared-feed-item.component.scss']
})
export class SharedFeedItemComponent implements OnInit {

  @Input() item:IFeedItem = feedItemDefaults;

  constructor() { }

  ngOnInit() {
  }

}



