import {Observable} from "rxjs";
import {AuthService} from "../auth/auth.service";
import {Router, CanActivateChild, CanActivate} from "@angular/router";
import {Injectable} from "@angular/core";

@Injectable()
export class AuthGuard implements CanActivateChild, CanActivate {

  constructor(private auth: AuthService, private router: Router) {}

  canActivateChild() {
    console.warn('AuthGuard canActivateChild');
    return this.canActivate();
  }

  canActivate() {
    console.warn('AuthGuard canActivate');
    return this.auth.auth().flatMap(
      result => {
        let isAuthorized = this.auth.isAuthorized();
        if (!isAuthorized) this.router.navigateByUrl('/auth')
        return Observable.of( isAuthorized )
      }
    );
  }
}



